@charset "UTF-8";
.section__slider {
  margin-top: 16px; }

@media (min-width: 768px) {
  .section__slider {
    height: 180px; }
  .slider {
    overflow: hidden;
    transition: .5s; }
  .section__slider--expand {
    height: auto; } }

.slider__wrapper {
  list-style: none;
  padding: 0; }

@media (min-width: 768px) {
  .slider__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin-left: -24px; } }

.slider-item {
  width: 240px;
  background-color: #fff;
  border-radius: 6px;
  border: 1px solid #EAEAEA;
  padding: 0 16px;
  box-sizing: border-box;
  transition-property: transform;
  flex-shrink: 0; }

.slider__item {
  margin-bottom: 28px; }

@media (min-width: 768px) {
  .slider-item {
    display: block;
    flex-shrink: 0;
    flex-grow: 0;
    flex-basis: 240px; }
  .slider__item {
    margin: 8px 0 8px 24px; } }

.slider-item__link {
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
  text-decoration: none;
  color: #000000; }

.slider-item__link:after {
  display: block;
  content: "";
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 2px solid #FF3E79;
  background-image: url(./static/arrow_icon2.svg);
  background-repeat: no-repeat;
  background-position: center center;
  position: absolute;
  right: 0; }

.slider-pagination {
  display: flex;
  justify-content: center; }

.slider .swiper-pagination-bullet {
  width: 12px;
  height: 12px; }

.slider .swiper-pagination-bullet-active {
  background-color: #B5B6BC; }

.expand-button {
  display: none;
  color: #000;
  text-decoration: none;
  font-family: 'TT Lakes Medium', sans-serif;
  font-size: 14pt;
  margin-top: 16px;
  border: none;
  background-color: transparent;
  padding-left: 30px;
  position: relative;
  cursor: pointer; }

.expand-button:before {
  content: "";
  width: 30px;
  height: 30px;
  position: absolute;
  background-image: url(./static/expand_icon.svg);
  background-position: 0 center;
  background-repeat: no-repeat;
  left: 0;
  top: 0;
  transition-duration: .5s; }

.expand-button--expanded:before {
  transform: rotate(180deg);
  top: -25%; }

@media (min-width: 768px) {
  .expand-button {
    display: block; } }

.main__brands-section {
  margin-top: 24px; }

.brands-section .slider-item {
  height: 72px; }

.brands-section .slider-item__link {
  align-items: center; }

.brands-section .slider-item__link:after {
  top: 50%;
  margin-top: -21px; }

.brands-section .expand-block {
  height: auto; }

@media (min-width: 768px) {
  .brands-section .expand-block {
    height: 170px; }
  .brands-section .expand-block--expanded {
    height: auto; } }

.main__types-section {
  margin-top: 24px; }

.types-section .slider-item {
  height: 160px;
  padding: 16px; }

.types-section .slider-item__link:after {
  bottom: 0; }

.types-section .expand-block {
  height: auto; }

@media (min-width: 768px) {
  .types-section .expand-block {
    height: 170px; } }

.types-section .expand-block--expanded {
  height: auto; }

.main__price-section {
  margin-top: 24px; }

@media (min-width: 768px) {
  .price-section .section__slider {
    height: auto; } }

@media (min-width: 768px) {
  .price-section .slider__wrapper {
    flex-direction: column;
    margin-left: 0; } }

.price-section .slider-item {
  height: 200px;
  width: 260px;
  padding: 16px 16px;
  position: relative; }

@media (min-width: 768px) {
  .price-section .slider__item {
    margin: 0; }
  .price-section .slider-item {
    width: 100%;
    max-height: 64px;
    border-radius: 0;
    box-sizing: border-box;
    border: none;
    padding: 0 24px; }
  .price-section .slider-item:nth-child(2) {
    border-radius: 6px 6px 0 0; }
  .price-section .slider-item:nth-child(n+3):before {
    content: "";
    width: 96%;
    height: 1px;
    background-color: #EAEAEA;
    position: absolute;
    top: 0;
    left: 2%; }
  .price-section .slider-item:last-of-type {
    border-radius: 0 0 6px 6px; } }

.price-section .slider-item--caption {
  display: none;
  border: none;
  background-color: transparent; }
  .price-section .slider-item--caption .slider-item__link:after {
    display: none; }
  @media (min-width: 768px) {
    .price-section .slider-item--caption {
      display: block; } }

.price-section .slider-item__link {
  flex-direction: column;
  font-size: 14px; }
  @media (min-width: 768px) {
    .price-section .slider-item__link {
      flex-direction: row;
      align-items: center; } }

.price-section .slider-item__link:after {
  content: "Заказать";
  color: #ffffff;
  background-color: #FF3E79;
  width: 119px;
  height: 32px;
  border-radius: 16px;
  display: flex;
  align-items: center;
  font-family: "TT Lakes Bold", sans-serif;
  padding: 0 8px;
  bottom: 0;
  box-sizing: border-box;
  background-image: url(./static/arrow_icon_white.svg);
  background-position: 90% center;
  text-transform: uppercase;
  font-size: 12px; }
  @media (min-width: 768px) {
    .price-section .slider-item__link:after {
      bottom: auto; } }

.price-section .slider-item__item-caption {
  color: #7E7E82;
  font-family: 'TT Lakes Medium', sans-serif; }
  @media (min-width: 768px) {
    .price-section .slider-item__item-caption {
      display: none; } }

@media (min-width: 768px) {
  .price-section .slider-item__item-caption:not(:nth-child(3n+1)) {
    margin-top: 16px; } }

@media (min-width: 768px) {
  .price-section .slider-item__item-value:nth-child(2) {
    width: 45%; } }

@media (min-width: 800px) {
  .price-section .slider-item__item-value:nth-child(2) {
    width: 50%; } }

@media (min-width: 768px) {
  .price-section .slider-item__item-value:nth-child(4) {
    width: 20%; } }

.price-section .section__price-description {
  display: block;
  color: #1B1C21;
  margin-top: 16px; }

.price-section .section__get-offer {
  display: block;
  margin-top: 16px;
  text-decoration: none;
  color: #000000;
  font-family: 'TT Lakes Medium', sans-serif;
  padding-top: 16px; }

.price-section .section__get-offer:hover {
  text-decoration: none; }
